}

@font-face {
  font-family: "Ubuntu";
  font-weight: 400;
  font-style: normal;
  src: local("Ubuntu-Regular"), url("../fonts/Ubuntu-Regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Ubuntu";
  font-weight: 700;
  font-style: normal;
  src: local("Ubuntu-Bold"), url("../fonts/Ubuntu-Bold.woff2") format("woff2");
  font-display: swap;
}
