  font-family: "Ubuntu";
  font-weight: 500;
  font-style: normal;
  src: local("Ubuntu-Medium"), url("../fonts/Ubuntu-Medium.woff2") format("woff2");
  font-display: swap;
}
html {
  box-sizing: border-box;
  /* 1 */
  line-height: 1.15;
  /* 2 */
  -webkit-text-size-adjust: 100%;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
li,
figure,
figcaption,
blockquote,
